import React from 'react'
import { useState, useEffect } from "react";
import { Navbar, Nav, Container } from "react-bootstrap";
import {
    BrowserRouter as Router
  } from "react-router-dom";

import styled from 'styled-components';
function NavAct() {
    const [activeLink, setActiveLink] = useState('home');
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const onScroll = () => {
      if (window.scrollY > 50) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    }

    window.addEventListener("scroll", onScroll);

    return () => window.removeEventListener("scroll", onScroll);
  }, [])

  const onUpdateActiveLink = (value) => {
    setActiveLink(value);
  }
  return (
    <NavActivity>
      <Navbar expand="md" className={scrolled ? "scrolled" : ""} >
        <Container>
          <Navbar.Brand href="https://aymenguedri.me/">
           <h1 className="TitleName"> <span>A</span>ymen<span>Guedri.</span>me</h1>
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" style={{background:"white"}} >
            <span className="navbar-toggler-icon" ></span>
          </Navbar.Toggle>
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ms-auto">
              <Nav.Link href="https://aymenguedri.me/" className={activeLink === 'home' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('home')}>Home</Nav.Link>
              <Nav.Link href="https://aymenguedri.me/" className={activeLink === 'home' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('About')}>About</Nav.Link>
              <Nav.Link href="https://aymenguedri.me/" className={activeLink === 'home' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('Service')}>Service</Nav.Link>
              <Nav.Link href="https://aymenguedri.me/" className={activeLink === 'skills' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('skills')}>Skills</Nav.Link>
              <Nav.Link href="https://aymenguedri.me/" className={activeLink === 'projects' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('projects')}>Projects</Nav.Link>
              <Nav.Link href="https://aymenguedri.me/" className={activeLink === 'activity' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('activities')}>Activities</Nav.Link>
              <Nav.Link href="https://aymenguedri.me/" className={activeLink === 'Contact' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('Contact')}>Contact</Nav.Link>

            </Nav>
            <span className="navbar-text">
              
              
            </span>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      </NavActivity>
    
  )
}

export default NavAct

const NavActivity =styled.div`
margin-bottom:10rem;
`;