import React from 'react'
import logo from "../assets/img/logo.png";
import navIcon1 from "../assets/img/nav-icon1.svg";
import navIcon2 from "../assets/img/nav-icon2.svg";
import navIcon3 from "../assets/img/nav-icon3.svg";
import { Container, Row, Col } from "react-bootstrap";
import Slide from 'react-reveal/Slide';

function Footer2() {
  return (
    <footer className="footer">
      <Container>
      <Slide top>
        <Row className="align-items-center">
          
          <Col size={12} sm={6}>
            <img src={logo} alt="Logo" style={{width:"250px"}} />
          </Col>
          <Col size={12} sm={6} className="text-center text-sm-end">
            <div className="social-icon">
              <a href="https://www.linkedin.com/in/aymenguedri/" target="_blank"><img src={navIcon1} alt="Icon" /></a>
              <a href="https://www.facebook.com/Aymenguedri.me" target="_blank"><img src={navIcon2} alt="Icon" /></a>
              <a href="https://www.instagram.com/aymenguedri.me/" target="_blank"><img src={navIcon3} alt="Icon" /></a>
            </div>
            <p>Copyright 2022. All Rights Reserved</p>
          </Col>
        </Row>
        </Slide>
      </Container>
    </footer>
  )
}

export default Footer2
