import React from 'react'
import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import IMG1 from "./assets/img/logo_png.png";
import IMG2 from "./assets/img/imgIoT.jpg";
import IMG3 from "./assets/img/jan22.png";
import IMG4 from "./assets/img/tew.jpg";
import IMG5 from "./assets/img/neuron.png";
import IMG6 from "./assets/img/cp2.png";
import IMG7 from "./assets/img/cp1.jpg";
import IMG8 from "./assets/img/system.jpg";
import IMG9 from "./assets/img/iq.jpg";
import Fade from 'react-reveal/Fade';
function Projects2() {

    const data=[
        {
            id:1,
            image:IMG1,
            description:"LynxCoder club",
            link1:"https://www.facebook.com/LynxCoder/",
            
        },
        {
            id:2,
            image:IMG2,
           
            description:"IOT Smart door PFA project",
            link1:"https://youtu.be/9dhrrhRhbis",
            
        },
        {
            id:3,
            image:IMG3,
            description:"Trainning for global game jam",
            link1:"https://youtu.be/AAruVtFAzpw",
            
        },
        {
            id:4,
            image:IMG4,
            description:"Tew Save the world ",
            link1:"https://www.globalgoals.org/",
            
        },
        {
            id:5,
            image:IMG5,
            description:"Mr neuron Global game jam 2018",
            link1:"https://globalgamejam.org/users/aymen-guedri",
            
        },
        {
            id:6,
            image:IMG7,
        
            description:"Soliya connect program project1",
            link1:"https://youtu.be/QkPhYnKfMvg",
            
        },
        {
            id:7,
            image:IMG6,
            description:"Soliya connect program project2",
            link1:"https://youtu.be/Q-yK2sm8AH4",
            
        },
        {
            id:8,
            image:IMG8,
            description:"Smart System for the elderly",
            link1:"https://www.facebook.com/photo/?fbid=2546183048850004&set=pcb.2546183222183320",
            
        },
        {
            id:9,
            image:IMG9,
            description:"Ambassador for IQ challenge",
            link1:"https://www.facebook.com/IQCHALLENGEJUNIORS/photos/285325915422361",
            
        },
    ]


  return (
    <section className="project" id="projects">
      <Container>
      <Fade right>
        <Row>
          <Col size={12}>
            
              <div>
              
                <h2>My Projects</h2>
                <br/>
                   <div className='container portfolio__container'>
                   {data.map(({id,image,description,link1})=>{
                    return(
                        <article key={id} className='portfolio__item'>
                     <div className='portfolio__item_image'> 
                       <img src={image} alt={description} /> 
                      </div>
                     
                     <p>{description}</p>
                       <div className='portfolio__item_cta'>
                           <a href={link1} className='btn btn-primary' target='_blank'>Learn More</a>
                           
                        </div>
                    </article>
                    )
                   }
                   
                   )}
                    </div>
                </div>
               
          </Col>
        </Row>
        </Fade>
      </Container>
      
    </section>
  )
}

export default Projects2
