import meter1 from "../assets/img/meter1.svg";
import meter2 from "../assets/img/meter2.svg";
import meter3 from "../assets/img/meter3.svg";
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import arrow1 from "../assets/img/arrow1.svg";
import arrow2 from "../assets/img/arrow2.svg";
import colorSharp from "../assets/img/color-sharp.png"
import image from "../assets/img/image.jpg";
import styled from "styled-components";
import Fade from 'react-reveal/Fade';


function About() {
   
  return (
    <div className="about" id="About">
    <Container>
    <Fade top>
    <div className="container">
    <div className="row">
                <div className="col-12">
                    <div className="skill-bx wow zoomIn">
                      <h5>Get to know</h5>
                      <h2>About me</h2>

                      <AboutContainer>
                        <Me>
                          
                            <img src="/images/aymen.jpg" alt="my photo" className="Image"/>
                          
                        </Me>

                        <Content>
                          <Cards>
                            <Card>
                              <article>
                                <img src="/images/linkedin.png" alt="linkedin" className="aboutimage" />
                                <h5>Linkedin</h5>
                                <small>aymenguedri</small>
                              </article>
                              </Card>

                              <Card>
                              <article>
                                <img src="/images/tel.png" alt="linkedin" className="aboutimage" />
                                <h5>Phone Number</h5>
                                <small>+216 24 361 713</small>
                              </article>
                              </Card>

                              <Card>
                              <article>
                                <img src="/images/mail.png" alt="linkedin" className="aboutimage" />
                                <h5>Mail</h5>
                                <small>aymenguedri@ieee.org</small>
                               </article>
                              </Card>

                           </Cards>
                           <p>My name is Aymen Guedri I'm 22 Years old , I live in Tunisia and I study computer system engineering at bizerte science faculty</p>

                           <a href="#Contact" className="btn btn-primary">Let's talk</a>
                        </Content>
                      </AboutContainer>

                    </div>
                 </div>
    </div>
    </div>
    </Fade>
    </Container>
    </div>
    
  )
}

export default About


const Container =styled.div`
padding: 0 0 50px 0;
position: relative;

h2{
  color:crimson;
  margin-bottom:1rem;
}



`;

const AboutContainer =styled.div`
display:grid;
grid-template-columns:35% 50%;
gap:15%;


@media(max-width:1024px){
  grid-template-columns:1fr;
  gap:0;
   
}

@media(max-width:600px){
  grid-template-columns:0.1fr;
  gap:0;
  
}


`;

const Me =styled.div`
width:100%;
aspect-ratio:1/1;
border-radius:2rem;
background-image: radial-gradient(circle, #be174b, #9c3165, #75406c, #544660, #444446);
display:grid;
place-items:center;

.Image{
border-radius:2rem;
overflow:hidden;
transform:rotate(10deg);
transition:2s
}

.Image:hover{
  transform:rotate(0deg);

  }

  @media(max-width:1024px){
    width:60%;
    margin:2rem auto 4rem;
  }
  @media(max-width:600px){
    width:80%;
    margin:0 2rem 3rem;
  }

  @media(max-width:900px){
    width:80%;
    margin:0 2rem 3rem;
  }

`;

const Content =styled.div`
p{
  font-size:1.3rem;
}

a{
  margin-top:1rem;
width:6rem;
font-size:1.3rem;
}

@media(max-width:1024px){
  margin:1.6rem 0 1.5rem;
}

@media(max-width:600px){
  
font-size:0.7rem;
  
}

@media(max-width:900px){
  font-size:0.7rem;
}

`;



const Cards =styled.div`
display:grid;
grid-template-columns:repeat(3,1fr);
gap:1.5rem;
margin-bottom:2rem;


@media(max-width:600px){
  grid-template-columns:1fr 1fr;
  gap:1rem;
}

@media(max-width:900px){
  grid-template-columns:1fr 1fr;
  gap:1rem;
}
`;

const Card =styled.div`
background-image: radial-gradient(circle, #2a4369, #4e5776, #6c6d84, #878592, #9f9ea1);
padding:2rem;
text-align:center;
border:1px solid transparent;
border-radius:1rem;
width:10rem;

.aboutimage{
  width:30px;
}

:hover{
  background:transparent;
  border-color:gray;
  cursor:default;
  transition:10s;
}

h5{
  font-size:1.3rem;
}

small{
  font-size:1rem;
  
}

@media(max-width:600px){
  width:8rem;

  h5{
    font-size:0.8rem;
  }
  small{
    font-size:0.7rem;
  }
}

@media(max-width:900px){
  width:8rem;
  h5{
    font-size:0.8rem;
  }
  small{
    font-size:0.7rem;
  }
}

`;

